.hero {
  height: 200px;
  background-size: cover;
  background-position: center;
  position: relative;
  border-radius: 4px;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
  border-radius: 4px;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: #FF9420;
  border: 20px;
  border-color: #000;
  text-shadow: 2px 2px #000;
}

.hero h1 {
  font-size: 60px;
  padding-top: 35px;
  margin-top: 0;
}


